
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


export default {
name: 'login-view',
components: { XHeader, Flexbox, FlexboxItem },
data () {
return {
websocket: '',
commodityName: '',
commodityNo: '',
contractValue: {},
contract_list: [],
abc: false
}
},
created () {
if (this.$route.query.securityType && this.$route.query.commodityNo) {
var arrayString = this.$route.query.securityType + '_' + this.$route.query.commodityNo + (this.$route.query.mainContractNo ? '_' : '') + this.$route.query.mainContractNo
this.contract_list.push(arrayString)
this.commodityNo = this.$route.query.securityType + this.$route.query.commodityNo + this.$route.query.mainContractNo
} else {
this.$router.replace({path: '/home'})
}
if (this.$route.query.commodityName) {
this.commodityName = this.$route.query.commodityName
}
this.init()
},
computed: {
average: function () {
return (this.contractValue[4] + this.contractValue[5]) ? ((this.contractValue[4] + this.contractValue[5]) / 2).toFixed(1) : ''
}
},
mounted () {
const that = this
window.onbeforeunload = function () {
that.websocket.close()
}
},
destroyed: function () {
this.websocket.close()
},
methods: {
init () {

if ('WebSocket' in window) {
this.websocket = new WebSocket('ws://quote.vs.com:8888')
}

const that = this
this.websocket.onmessage = function (event) {
if (JSON.parse(event.data).method === 'on_rtn_quote') {
that.contractValue = JSON.parse(event.data).data
}
that.heart().reset()
}
this.websocket.onclose = function (event) {
that.websocket = new WebSocket('ws://quote.vs.com:8888')
}
this.websocket.onopen = function (event) {
that.pushData()
that.heart().start()
}
},
pushData () {
var that = this
this.websocket.send(JSON.stringify({
'method': 'req_subscribe',
'req_id': '1231',
'data': {'contract_list': that.contract_list}
}))
},
heart () {
var _this = this
var timeout = 60000
var timeoutObj = null
return {
reset: function () {
clearTimeout(timeoutObj)
_this.heart().start()
},
start: function () {
_this.timeoutObj = setTimeout(function () {
_this.websocket.send(JSON.stringify({
'method': 'req_heartbeat',
'req_id': '1231',
'data': {}
}))
}, timeout)
}
}
}
}
}



